<template>
  <div class="con">
    <h2>登录</h2>
    <div>
      角色：<select  v-model="user.type">
<option value="1">会员</option>
<option value="2">外卖小哥</option>
      </select>
    </div>
    <div>
      账号：<input type="text" v-model="user.name">
    </div>
  <div>
    密码:
    <input type="text" v-model="user.pass">
  </div>
  <button @click="login">登录</button>
  <!-- 路由导航 -->
  <!-- <router-link to='/index'>
           首页
  </router-link> -->
  </div>
</template>

<script>
import {reqLogin} from '../../utils/http'
import axios from 'axios'
export default {
   data() {
    return {
      user:{
        type:1,
        name:'',
        pass:''
      }
    }
  },
methods: {

  login(){
   reqLogin(this.user.name,this.user.pass).then(res=>{

        if(res.data.isok){
        localStorage.setItem("isLogin",1)
          //存token
           localStorage.setItem("type",this.user.type)
          localStorage.setItem("token",res.data.token)

          this.$router.push("/index")
        }else{
          alert(res.data.info)
        }
      })
  }
},
}
</script>

<style>

</style>



